// log
import store from "../store";
import { config } from "../../config";

const fetchDataRequest = () => {
  return {
    type: "CHECK_DATA_REQUEST",
  };
};

const fetchDataSuccess = (payload) => {
  return {
    type: "CHECK_DATA_SUCCESS",
    payload: payload,
  };
};

const fetchDataFailed = (payload) => {
  return {
    type: "CHECK_DATA_FAILED",
    payload: payload,
  };
};

const tokenID = (config.farmingIDs)

export const fetchData = () => {
  return async (dispatch) => {
    dispatch(fetchDataRequest());
    try {
      let totalSupply = await store
        .getState()
        .blockchain.smartContract.methods.totalSupply()
        .call();

        let tokenCount = await store
        .getState()
        .blockchain.smartContract.methods.tokenCount()
        .call();

        let ownerOf = await store 
        .getState()
        .blockchain.smartContract.methods.ownerOf(172)
        .call();
    

        let balanceOf = await store
        .getState()
        .blockchain.smartContract.methods.balanceOf(ethereum.selectedAddress)
        .call();

      dispatch(
        fetchDataSuccess({
          totalSupply, tokenCount, ownerOf, balanceOf,
        })
      );
    } catch (err) {
      console.log(err);
      dispatch(fetchDataFailed("Could not load data from contract."));
    }
  };
};
